export function configureWebchat(ThemeColor: string, allowUpload: string, attachmentBackgroundColor: string, WebchatFont: string) {


  let hiddeUploadBool: boolean;

  if (allowUpload === "true") {
    hiddeUploadBool = false;
  } else {
    hiddeUploadBool = true;
  }

  const webchatContainerProps = {
    containerStyles: {
      height: "490px",
      width: "360px",
      zIndex: 100,
      overflowY: "hidden",
      "@media (max-width :480px)": {
        "width": "100%",
        "minHeight": "calc(90vh - 80px)",
        "border-radius": "0",
      },
      ".webchat__icon-button__shade": {
        "width": "auto"
      }
    },
    webChatStyles: {
      bubbleBackground: ThemeColor,
      hideUploadButton: hiddeUploadBool,
      notificationText: "#F7F7F9",
      failedConnectivity: "#F7F7F9",
      slowConnectivity: "#F7F7F9",
      sendBoxPlaceholderColor: "#ffffff",
      primaryFont: WebchatFont,
    },
    renderingMiddlewareProps: {
      typingIndicatorMessageStyleProps: {
        fontFamily: WebchatFont,
      },
      avatarTextStyleProps: {
        fontFamily: WebchatFont,
      },
      timestampContentStyleProps: {
        fontFamily: WebchatFont,
      },
      timestampFailedTextStyleProps: {
        fontFamily: WebchatFont,
      },
      timestampRetryTextStyleProps: {
        fontFamily: WebchatFont,
      },
      avatarStyleProps: {
        backgroundColor: ThemeColor
      },
      attachmentStyles: {
        background: attachmentBackgroundColor,
      },
      userMessageStyleProps: {
        fontSize: "14px"
      },
      systemMessageStyleProps: {
        fontFamily: WebchatFont,
      },
      attachmentIconStyles: {
        direction: "ltl",
        padding: "0 0 0 20px",
        lineHeight: 0
      }
    },
    localizedTexts: {
      MIDDLEWARE_TYPING_INDICATOR_ONE: "{0} er að skrifa ...",
      MIDDLEWARE_MESSAGE_SENDING: "Sendist ...",
      MIDDLEWARE_MESSAGE_DELIVERED: "Sent",
      MIDDLEWARE_BANNER_FILE_NULL_ERROR: "Villa við að bæta við skjali, vinsamlegast reyndu aftur",
      MIDDLEWARE_BANNER_FILE_SIZE_WITHOUT_EXTENSION_ERROR: "Þetta skjal er of stórt, skjöl mega mest vera {0} MB",
      MIDDLEWARE_BANNER_FILE_SIZE_EXTENSION_ERROR: "Skjöl mega mest vera {0} MB og {1} skjöl eru ekki studd",
      MIDDLEWARE_BANNER_FILE_WITHOUT_EXTENSION: "Mistókst að bæta við skjali, vinsamlegast reyndu aftur",
      MIDDLEWARE_BANNER_FILE_EXTENSION_ERROR: "{0} skjöl eru ekki studd",
      MIDDLEWARE_BANNER_FILE_SIZE_ERROR: "Þetta skjal er of stórt, skjöl mega mest vera {0} MB",
      MIDDLEWARE_BANNER_FILE_IS_EMPTY_ERROR: "Þetta skjal er tómt og því ekki hægt að hlaða því upp, reyndu aftur",
      MIDDLEWARE_BANNER_ERROR_MESSAGE: "Mistókst að bæta við skjali, vinsamlegast reyndu aftur",
      MIDDLEWARE_BANNER_INTERNET_BACK_ONLINE: "Þú ert tengd/ur aftur",
      MIDDLEWARE_BANNER_NO_INTERNET_CONNECTION: "Næ ekki tengingu - Kannaðu netsambandið og reyndu aftur",
      MIDDLEWARE_MAX_CHARACTER_COUNT_EXCEEDED: "Þessi skilaboð eru of löng - Vinsamlegast styttu þau áður en þú sendir",
      MIDDLEWARE_TYPING_INDICATOR_TWO: "{0} og {1} eru að skrifa ...",
      MIDDLEWARE_MESSAGE_NOT_DELIVERED: "Ekki sent",
      MIDDLEWARE_MESSAGE_RETRY: "Reyndu aftur",
      PRECHAT_REQUIRED_FIELD_MISSING_MESSAGE: "Þú verður að fylla út {0}",
      MIDDLEWARE_TYPING_INDICATOR_MULTIPLE: "{0} fulltrúar eru að skrifa ...",
    }
  }
  return webchatContainerProps;
}
