export function configureFooter(allowDownloadTranscript: string, allowEmailtranscript:string, ThemeColor: string) {

    let hideDownloadBool: boolean;
    let hideEmailBool: boolean;

    if (allowDownloadTranscript === "true") {
        hideDownloadBool = false;
    } else {
        hideDownloadBool = true;
    }

    if (allowEmailtranscript === "true") {
        hideEmailBool = false;
    } else {
        hideEmailBool = true;
    }

    const footerProps = {
        //Styles
        styleProps: {
            downloadTranscriptButtonStyleProps: {
                color:"#3370B7",
            },
            emailTranscriptButtonStyleProps: {
                color: "#3370B7", 
            },
            audioNotificationButtonStyleProps: {
                    color: "#3370B7",
            },
            downloadTranscriptButtonHoverStyleProps: {
                filter: "none",
                backgroundColor: "#F3F2F1",
            },
            audioNotificationButtonHoverStyleProps: {
                filter: "none",
                backgroundColor: "#F3F2F1",
            },
            emailTranscriptButtonHoverStyleProps: {
                filter: "none",
                backgroundColor: "#F3F2F1",
            },
        },
        //Controls
        controlProps: {
            hideDownloadTranscriptButton: hideDownloadBool,
            hideEmailTranscriptButton: hideEmailBool,
            downloadTranscriptButtonProps: {
                ariaLabel: "Sækja afrit af spjallinu",
                text: "Sækja afrit af spjallinu",
            },
            audioNotificationButtonProps: {
                ariaLabel: "Slökkva á hljóði",
                toggleAriaLabel: "Kveikja á hljóði",
            },
            emailTranscriptButtonProps: {
                ariaLabel: "Fá sent afrit af þessu spjalli",
                toggleAriaLabel: "Fá sent afrit af þessu spjalli",
            }
        },
    }

    return footerProps;
}