import React from 'react';
import { LiveChatWidget } from "@microsoft/omnichannel-chat-widget";
import { OmnichannelChatSDK } from "@microsoft/omnichannel-chat-sdk";
import { useState, useEffect } from 'react';
import { configurePrechat } from './configs/configurePrechat';
import { configureChatButton, configureClosedChatButton } from './configs/configureChatButton';
import { configureClosedHeader, configureHeader } from './configs/configureHeader';
import { configureClosedPane, configureLoadingPane } from './configs/configureLoadingPane';
import { configureWebchat } from './configs/configureWebchatContainer';
import { configureFooter } from './configs/configureFooter';
import { configureConfirmationPaneProps } from './configs/configureConfirmationPane';
import { configureEmailTranscript } from './configs/configureEmailTranscript';


//const ThemeColor = "#3370B7" //prod
//const ThemeColor = "#f08700" //uat
const ThemeColor = "#dc2919" //dev
const attachmentBackgroundColor = "#e7efff";
const ChatButtonTitleFont = "Klavika,sans-serif";
const ChatButtonSubTitleFont = "Inter, Arial, sans-serif";
const CustomButtonFont = "Klavika,sans-serif";
const HeaderFont = "Klavika,sans-serif";
const LoadingPaneFont = "Inter, Arial, sans-serif";
const WebchatFont = "Inter, Arial, sans-serif";
const emailTranscriptPaneFont = "Inter, Arial, sans-serif";

function App() {
  const [liveChatWidgetProps, setLiveChatWidgetProps] = useState<any>();

  useEffect(() => {

    const chatSDKConfig = {
      telemetry: { disable: true },
      persistentChat: {
        disable: false,
        tokenUpdateTime: 900000,
      },
    };

    const init = async () => {

      // Eimskip DEV - opið 08-17
      /*
      const omnichannelConfig = {
        orgId: "f04458bc-1496-4d75-b5f3-29aed957248d",
        orgUrl: "https://orga570f4a0-crm4.omnichannelengagementhub.com",
        widgetId: "a2515e20-4f7c-4b52-97d0-7bb4e422f420"
      };
      */

      // Eimskip DEV - adam test - opið 08-17
      
      const omnichannelConfig = {
        orgId: "f04458bc-1496-4d75-b5f3-29aed957248d",
        orgUrl: "https://orga570f4a0-crm4.omnichannelengagementhub.com",
        widgetId: "aed68a3c-d1ff-469a-b035-54d37171ddfc"
      };
      //--------------------------------------------------------------------------------------------------------
      //muna eftir send takkanum: breyta "TEXT_INPUT_SEND_BUTTON_ALT":"Send" í "TEXT_INPUT_SEND_BUTTON_ALT":"Senda" í 2.chunk skránni eða ekki breyta skránni
      // Eimskip UAT - Mónika test 
      // const omnichannelConfig = {
      //   orgId: "d91ac080-1b98-4f90-aeb9-e0b9016aee7e",
      //   orgUrl: "https://orgc619eff3-crm4.omnichannelengagementhub.com",
      //   widgetId: "61c73613-a389-46c5-aae7-74b928bb1c4c"
      // };

      // Eimskip PROD - Mónika test 
      // const omnichannelConfig = {
      //   orgId: "700ca145-5341-462c-b87d-2dccd44adcc6",
      //   orgUrl: "https://org3e943a7c-crm4.omnichannelengagementhub.com",
      //   widgetId: "adff299f-ce95-40c1-a4ba-e8a9fcf4a6f6"
      // };

      const chatSDK = new OmnichannelChatSDK(omnichannelConfig, chatSDKConfig);
      await chatSDK.initialize(); // mandatory
      const chatConfig = await chatSDK.getLiveChatConfig();
      const preChatSurvey = await chatSDK.getPreChatSurvey();
      //console.log(preChatSurvey)
      chatConfig.maxUploadFileSize = "50MB";

      // Chat titles and Logo
      var mainTitle = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_widgettitle;
      var subTitle = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_widgetsubtitle;
      var closedTitle = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_offlinewidgettitle;
      var closedSubTitle = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_offlinewidgetsubtitle;
      //var logo = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_avatarurl;
      var allowUpload = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_enablefileattachmentsforcustomers;
      var allowDownloadtranscript = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_enablechattranscriptdownload;
      var allowEmailtranscript = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_enablechattranscriptemail;

      // is prechat enabled?
      var prechatEnabled = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_prechatenabled;

      // show widget during offline hours
      var showChatOffline = chatConfig.LiveWSAndLiveChatEngJoin.msdyn_showwidgetduringofflinehours;


      console.log(chatConfig.LiveWSAndLiveChatEngJoin);
      

      const { LiveWSAndLiveChatEngJoin: liveWSAndLiveChatEngJoin } = chatConfig;
      const { OutOfOperatingHours: outOfOperatingHours } = liveWSAndLiveChatEngJoin;


      // Widget stillingar
      const liveChatWidgetProps = {

        // Chat Búbbla
        chatButtonProps: configureChatButton(mainTitle, subTitle, ThemeColor, ChatButtonTitleFont, ChatButtonSubTitleFont),

        // Pre chat survey styling
        preChatSurveyPaneProps: configurePrechat(preChatSurvey, prechatEnabled, ThemeColor, CustomButtonFont),

        // Header stillingar
        headerProps: configureHeader(ThemeColor, HeaderFont),

        //Footer stillingar
        footerProps: configureFooter(allowDownloadtranscript,allowEmailtranscript, ThemeColor),

        // Loading skjár
        loadingPaneProps: configureLoadingPane(ThemeColor, LoadingPaneFont),

        // Loading skjár
        confirmationPaneProps: configureConfirmationPaneProps(ChatButtonTitleFont, ChatButtonSubTitleFont, CustomButtonFont),
        
        // Chat Búbbla Lokuð
        outOfOfficeChatButtonProps: configureClosedChatButton(closedTitle, closedSubTitle, ThemeColor, ChatButtonTitleFont, ChatButtonSubTitleFont),

        // Lokaður header
        outOfOfficeHeaderProps: configureClosedHeader(HeaderFont),

        // Lokaður Texti
        outOfOfficeHoursPaneProps: configureClosedPane(LoadingPaneFont),

        //Webchat container
        webChatContainerProps: configureWebchat(ThemeColor, allowUpload, attachmentBackgroundColor, WebchatFont),

        // Email Transcript Pane
        emailTranscriptPane: configureEmailTranscript(emailTranscriptPaneFont),

        // telemetryConfig
        telemetryConfig: {
          //telemetryDisabled : true,
          disableConsoleLog: true,
        },

        //Stærð á div glugga
        styleProps: {
          generalStyles: {
            zIndex: 999,
            //height: "calc(100%-25px)",
            bottom: "20px",
            right: "20px",
            position: "fixed",
            "@media (max-width :480px)": {
              "right": "0",
              "bottom": "0",
              "left": "0",
              "margin": "3px"
            }
          },
          generalStylesProps: {
            background: ThemeColor,
          },
        },

        //mandatory fields
        chatSDK: chatSDK, // mandatory
        chatConfig: chatConfig, // mandatory

      };
      
      if (outOfOperatingHours === "False") {
        setLiveChatWidgetProps(liveChatWidgetProps);
      } else if (outOfOperatingHours === "True") {
        if (showChatOffline === "true") {
          setLiveChatWidgetProps(liveChatWidgetProps);
        }
      }

    }

    init();
  }, []);

  return (
    liveChatWidgetProps && <LiveChatWidget {...liveChatWidgetProps} />
  );
}

export default App;

/* <div className="App">
      {liveChatWidgetProps && <LiveChatWidget {...liveChatWidgetProps} />}
    </div> */