import { ModernChatIconBase64 } from './logos'

export function configureChatButton(mainTitle: string, subTitle: string, ThemeColor: string, ChatButtonTitleFont: string, ChatButtonSubTitleFont: string) {

    const chatButtonProps = {
        controlProps: {
            titleText: mainTitle,
            subtitleText: subTitle,
            hideChatSubtitle: true,
            hideChatTitle: true,
            hideChatTextContainer: true,
            unreadMessageString: "Ný skilaboð",
            ariaLabelUnreadMessageString: "Þú hefur fengið ný skilaboð",
        },
        styleProps: {
            generalStyleProps: {
                height: "60px",
                width: "60px",
                borderRadius: "100px 100px 100px 99px",
                position: "absolute",
                backgroundColor: "#fff",
                borderColor: "#fff",
                borderStyle: "solid",
                borderWidth: "1px",
                boxShadow: "0 0 4px rgb(102 102 102 / 50%)",
                margin: "3px 3px 3px 3px",
                cursor: "pointer",
                bottom: "0px",
                display: "flex",
                right: "0px",
                padding: "0px",
                selectors: {
                    ":hover": {
                        backgroundColor: "lightgrey"
                    },
                    ":focus": {
                        outline: "none"
                    }
                }
            },
            iconStyleProps: {
                backgroundColor: ThemeColor,
                //backgroundColor: "#fff",
                backgroundImage: `url(${ModernChatIconBase64})`,
                borderStyle: "solid",
                borderRadius: "50%",
                borderColor: "transparent",
                borderWidth: "2px",
                align: "center",
                width: "60px",
                height: "60px",
                margin: "-2px -2px -2px -3px",
                justifyContent: "center",
                backgroundSize: "65% 65%",
            },
            titleStyleProps: {
                fontSize: "16px",
                fontWeight: "bold",
                padding: "2px 0px",
                height: "22px",
                color: "#262626",
                fontFamily: ChatButtonTitleFont,
            },
            subtitleStyleProps: {
                fontSize: "12px",
                fontWeight: "300",
                color: "#666",
                fontFamily: ChatButtonSubTitleFont,
            }
        }
    }
    return chatButtonProps;
}

export function configureClosedChatButton(closedTitle: string, closedSubTitle: string, ThemeColor: string, ChatButtonTitleFont: string, ChatButtonSubTitleFont: string) {

    const chatButtonProps = {

        controlProps: {
            titleText: closedTitle,
            subtitleText: closedSubTitle,
            hideChatSubtitle: true,
            hideChatTitle: true,
            hideChatTextContainer: true
        },
        styleProps: {
            generalStyles: {
                bottom: "20px",
                right: "20px",
                position: "fixed"
            },
            generalStyleProps: {
                height: "60px",
                width: "60px",
                borderRadius: "100px 100px 100px 99px",
                position: "absolute",
                backgroundColor: "#fff",
                borderColor: "#fff",
                borderStyle: "solid",
                borderWidth: "1px",
                boxShadow: "0 0 4px rgb(102 102 102 / 50%)",
                margin: "3px 3px 3px 3px",
                cursor: "pointer",
                bottom: "0px",
                display: "flex",
                right: "0px",
                padding: "0px",
                zIndex: 999,
                selectors: {
                    ":hover": {
                        backgroundColor: "lightgrey"
                    },
                    ":focus": {
                        outline: "none"
                    }
                }
            },
            iconStyleProps: {
                //backgroundColor: ThemeColor,
                backgroundColor: "#5a5d5e",
                backgroundImage: `url(${ModernChatIconBase64})`,
                borderStyle: "solid",
                borderRadius: "50%",
                borderColor: "transparent",
                borderWidth: "2px",
                align: "center",
                width: "60px",
                height: "60px",
                margin: "-2px -2px -2px -3px",
                justifyContent: "center",
                backgroundSize: "65% 65%",
            },
            titleStyleProps: {
                padding: "2px 0px",
                fontSize: "16px",
                fontWeight: "bold",
                height: "22px",
                color: "#262626",
                fontFamily: ChatButtonTitleFont,
            },
            subtitleStyleProps: {
                fontSize: "12px",
                fontWeight: "300",
                color: "#666",
                fontFamily: ChatButtonSubTitleFont,
            }
        }
    }
    return chatButtonProps;
}