import { ModernChatIconBase64 } from './logos'

export function configureHeader(ThemeColor: string, HeaderFont:string) {
    
    const headerProps = {
        //Styles
        styleProps: {
            generalStyleProps: {
                background: ThemeColor,
                borderRadius: "4px 4px 0 0",
                padding: "5px",
                Height: "30px",
                width: "100%",
                minWidth: "250px",
                "@media (max-width:480px)": {
                    "width": "100%",
                    "height": "40px",
                    "border-radius": "0"
                }
            },
            closeButtonHoverStyleProps: {
                color: "transparent",
                backgroundColor: ThemeColor,
                filter: "brightness(0.9)"

            },
            minimizeButtonHoverStyleProps: {
                filter: "brightness(0.9)",
                backgroundColor: ThemeColor,
                color: "transparent",
            },
            titleStyleProps: {
                fontSize: "20px",
                fontFamily: HeaderFont,
            },
        },
        //controls
        controlProps: {
            headerTitleProps: {
                text: "Netspjall Eimskips"
            },
            closeButtonProps: {
                ariaLabel: "Loka",
                text: "Loka",
            },
            minimizeButtonProps: {
                ariaLabel: "Minnka",
                text: "Minnka",
            },
            headerIconProps: {
                src: ModernChatIconBase64,
            },
        },
    }
    return headerProps;
}

export function configureClosedHeader(HeaderFont:string) {
    const headerProps = {
        controlProps: {
            headerTitleProps: {
                text: "Því miður er lokað"
            },
            minimizeButtonProps: {
                ariaLabel: "Minnka",
                text: "Minnka",
            },
            headerIconProps: {
                src: ModernChatIconBase64,
            },
        },
        styleProps: {
            minimizeButtonHoverStyleProps: {
                filter: "brightness(0.9)",
                backgroundColor: "#808080",
                color: "transparent",
            },
            titleStyleProps: {
                fontSize: "16px",
                fontFamily: HeaderFont,
            },
            generalStyleProps: {
                borderRadius: "0px",
                backgroundColor: "#5a5d5e",
                padding: "5px",
                height: "30px",
                width: "100%",
                minWidth: "250px",
                "@media (max-width :480px)": {
                    "width": "100%",
                    "height": "40px",
                    "border-radius": "0",
                    "margin-top": "3px"
                }
            },
        }
    }
    return headerProps;
}