export function configurePrechat(preChatSurvey: { body: string | any[]; actions: { title: string; }[]; }, prechatEnabled: any, ThemeColor: string, CustomButtonFont: string) {

    let preChatSurvey2 = "";

    if (prechatEnabled) {
        //Breytum Pre-questions labels í íslensk
        let preChatLength: number = preChatSurvey.body.length;
        //preChatSurvey.body[0].text = "Vinsamlegast svaraðu eftirfarandi:";
        preChatSurvey.body[0].text = "";
        //preChatSurvey.body[preChatLength - 1].text = "Reitir merktir * verður að fylla inn";
        preChatSurvey.body[preChatLength - 1].text = "";
        preChatSurvey.actions[0].title = "SENDA";

        // Bætum við að spurningar eru required ef við á og hendum í error message
        for (let i = 0; i < preChatLength; i++) {
            let strTmp = preChatSurvey.body[i].type;
            if (strTmp.includes("Input")) {
                if(preChatSurvey.body[i].hasOwnProperty("title")){
                    preChatSurvey.body[i]["wrap"] = true;
                    if(preChatSurvey.body[i].hasOwnProperty("value")){
                        if(preChatSurvey.body[i]["value"] === "false"){
                            preChatSurvey.body[i]["value"] = false;
                        }else{
                            preChatSurvey.body[i]["value"] = true;
                        }
                    }
                    if(preChatSurvey.body[i].hasOwnProperty("valueOff")){
                        if(preChatSurvey.body[i]["valueOff"] === "False"){
                            preChatSurvey.body[i]["valueOff"] = false;
                        }else{
                            preChatSurvey.body[i]["valueOff"] = true;
                        }
                    }
                    if(preChatSurvey.body[i].hasOwnProperty("valueOn")){
                        if(preChatSurvey.body[i]["valueOn"] === "False"){
                            preChatSurvey.body[i]["valueOn"] = false;
                        }else{
                            preChatSurvey.body[i]["valueOn"] = true;
                        }
                    }
                }
                let strTmp1 = preChatSurvey.body[i].id;
                if (strTmp1.includes("\"IsRequired\":true")) {
                    preChatSurvey.body[i]["isRequired"] = true;
                    preChatSurvey.body[i]["errorMessage"] = "Vinsamlegast fyllið út";
                }
                else {
                    preChatSurvey.body[i]["isRequired"] = false;
                }
            }
        }

        //Skiptum út unicode character fyrir stjörnu í venjulega
        let preChatSurvey1 = JSON.stringify(preChatSurvey);
        var re = /&#42;/gi;
        preChatSurvey2 = preChatSurvey1.replace(re, "").replace("1.1","1.3");
    }

    const preChatSurveyPaneProps = {
        controlProps: {
            payload: preChatSurvey2,
            adaptiveCardHostConfig: "{\"fontFamily\":\"Inter, Arial, sans-serif\",\"font-size\":\"20px\",\"containerStyles\":{\"default\":{\"foregroundColors\":{\"default\":{\"default\":\"#000000\"}},\"backgroundColor\":\"#FFFFFF\"}},\"actions\":{\"actionsOrientation\":\"Vertical\",\"actionAlignment\":\"stretch\"}}",
        },
        styleProps: {
            generalStyleProps: {
                borderStyle: "solid",
                borderRadius: "4px",
                borderWidth: "3px",
                backgroundColor: "#FFFFFF",
                borderColor: "#F1F1F1",
                overflowY: "auto",
                height: "520px",
                width: "360px",
                "@media (max-width:480px)": {
                    "width": "98%",
                    "height": "calc(90vh - 50px)",
                    "border-radius": "0",
                },
                "div select": {
                    "height": "40px"
                },
                ".ac-input":{
                    "display": "-webkit-inline-box!important",
                    "padding-right":"5px"
                },
            },
            customButtonStyleProps: {
                backgroundColor: ThemeColor,
                color: "#FFFFFF",
                fontFamily: CustomButtonFont,
                fontSize: "20px",
                height: "48px"
            },
            adaptiveCardContainerStyleProps: {
                border: "1px solid #ECECEC",
                borderRadius: "4px",
                margin: "3%",
                ".ac-container.ac-adaptiveCard:focus-visible": {
                    "outline": "none"
                }
            }
        }
    }
    return preChatSurveyPaneProps;
}