import { ModernChatIconBase64 } from './logos'

export function configureLoadingPane(ThemeColor: string, LoadingPaneFont: string) {
    const LoadingPaneProps = {
        styleProps: {
            generalStyleProps: {
                backgroundColor: ThemeColor,
                width: "360px",
                height: "520px",
                "@media (max-width :480px)": {
                    "width": "100%",
                    "minHeight": "calc(90vh - 50px)",
                    "border-radius": "0",
                }
            },
            iconStyleProps: {
                borderRadius: "0%",
                backgroundColor: ThemeColor,
                boxShadow: "none",
            },
            iconImageProps: {
                src: ModernChatIconBase64,
                width: "70px",
                height: "70px",
            },
            titleStyleProps: {
                fontSize: "18px",
                fontFamily: LoadingPaneFont,
            },
            subtitleStyleProps: {
                fontSize: "20px",
                fontFamily: LoadingPaneFont,
            },
            spinnerStyleProps: {
                width: "92px",
                height: "92px",
            },
            spinnerTextStyleProps: {
                fontSize: "12px",
                fontFamily: LoadingPaneFont,
            },
        },
        controlProps: {
            titleText: "Netspjall Eimskips",
            //subtitleText: "á spjallið ...",
            subtitleText: "",
            spinnerText: "Hinkrið aðeins ..."
        }
    }
    return LoadingPaneProps;
}

export function configureClosedPane(LoadingPaneFont: string) {

    const closedPane = {
        controlProps: {
            titleText: "Spjallið okkar er opið alla virka daga 8-16 nema föstudaga 8-15",
        },
        styleProps: {
            generalStyleProps: {
                justifyContent: "center",
                alignItems: "stretch",
                borderStyle: "solid",
                borderRadius: "4px",
                borderWidth: "3px",
                backgroundColor: "#FFFFFF",
                borderColor: "#F1F1F1",
                overflowY: "auto",
                margin: "3px",
                width: "360px",
                height: "520px",
                "@media (max-width :480px)": {
                    "width": "100%",
                    "minHeight": "calc(90vh - 50px)",
                    "border-radius": "0",
                }
            },
            titleStyleProps: {
                fontFamily: LoadingPaneFont,
                fontWeight: "normal",
                fontSize: "14px",
                color: "#000000",
                textAlign: "center",
                alignSelf: "auto"
            }
        }
    }
    return closedPane;
}